import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  TemplateRef,
  ViewChild
} from '@angular/core';
import { IManagedObject, IOperation, QueriesUtil } from '@c8y/client';
import {
  gettext,
  ISelectModalObject,
  IUpdateItemEvent,
  ModalSelectionMode
} from '@c8y/ngx-components';
import {
  DeviceSoftware,
  DeviceSoftwareChange,
  FilterCriteria,
  RepositorySelectModalComponent,
  RepositoryService,
  RepositoryType
} from '@c8y/ngx-components/repository/shared';
import { get } from 'lodash-es';
import { BsModalService } from 'ngx-bootstrap/modal';
import { BehaviorSubject, combineLatest, from, Observable, of, pipe, Subject } from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  map,
  mergeMap,
  shareReplay,
  switchMap,
  takeUntil,
  tap
} from 'rxjs/operators';

@Component({
  selector: 'c8y-installed-software',
  templateUrl: 'installed-software.component.html'
})
export class InstalledSoftwareComponent implements OnDestroy, OnInit {
  @Input() device: IManagedObject;
  @Input() softwareList: DeviceSoftware[];
  @Input() deviceSoftwareChanges: DeviceSoftwareChange[];
  @Input() deviceSoftwareChangesOperation: IOperation;
  @Input() deviceSoftwareChangesInProgress: boolean;
  @Input() typesQuery: object;
  @Output() changes = new EventEmitter<DeviceSoftwareChange[]>();
  @Output() showSoftwareChanges = new EventEmitter<void>();

  @ViewChild('alreadyInstalledWarning', { static: true })
  alreadyInstalledWarningTemplate: TemplateRef<any>;
  alreadyInstalledMessage = gettext(
    '{{ name }} (v. {{ version }}) is already installed on this device'
  );
  @ViewChild('loading', { static: true })
  loadingTemplate: TemplateRef<any>;

  @ViewChild('softwareType', { static: true })
  softwareTypeTemplate: TemplateRef<any>;

  showFilter: boolean = false;
  supportsSoftwareOperations: boolean = false;
  textFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  softwareTypeFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  filterCriteria$: Observable<FilterCriteria>;

  private readonly operationTypes = ['c8y_SoftwareUpdate', 'c8y_SoftwareList', 'c8y_Software'];

  private queriesUtil: QueriesUtil;
  private modalSearch: (filterCriteria: FilterCriteria) => void;
  private destroyed$: Subject<void> = new Subject();

  constructor(private repository: RepositoryService, private bsModal: BsModalService) {
    this.filterCriteria$ = combineLatest([this.textFilter$, this.softwareTypeFilter$]).pipe(
      map(([textFilter, softwareTypeFilter]) => ({
        name: textFilter,
        softwareType: softwareTypeFilter
      })),
      map(filterCriteria =>
        !filterCriteria.name && !filterCriteria.softwareType ? null : filterCriteria
      )
    );
    this.queriesUtil = new QueriesUtil();
  }

  ngOnInit(): void {
    const supportedOperations = get(this.device, 'c8y_SupportedOperations', []);
    this.supportsSoftwareOperations = this.operationTypes.some(
      operationType => supportedOperations.indexOf(operationType) > -1
    );
  }

  installSoftware() {
    const { resultEmitter, choiceEmitter, updateInstallableList$ } =
      this.displaySoftwareSelectModal({
        title: gettext('Install software'),
        subTitle: gettext('Available softwares matching the device type'),
        labels: { ok: gettext('Install') },
        showAdditionalFilter: true,
        additionalFilterTemplate: this.softwareTypeTemplate,
        repositoryEntriesWithVersions$: of([]),
        repositoryEntriesWithVersionsFn$: modal =>
          this.getInstallableSoftwareListWithVersions$(modal.content.searchTerm)
      });

    resultEmitter.pipe(takeUntil(this.destroyed$)).subscribe(softwareToInstall => {
      this.emitSoftwareInstall(softwareToInstall);
      this.showSoftwareChanges.emit();
    });

    choiceEmitter
      .pipe(
        this.isSoftwareInstalledOnDevicePipe(updateInstallableList$),
        takeUntil(this.destroyed$)
      )
      .subscribe(({ item }) =>
        updateInstallableList$.next({
          object: item,
          template: this.alreadyInstalledWarningTemplate,
          mapper: object => {
            object.installed = true;
            return object;
          }
        })
      );
  }

  updateSoftware(softwareToUpdate) {
    const { resultEmitter, choiceEmitter, updateInstallableList$ } =
      this.displaySoftwareSelectModal({
        title: gettext('Update software'),
        subTitle: gettext('Select one of the available software versions'),
        labels: { ok: gettext('Update') },
        noItemsMessage: gettext('No other software versions available.'),
        showFilter: false,
        repositoryEntriesWithVersions$: this.getSingleSoftwareWithVersions$(softwareToUpdate)
      });

    resultEmitter.pipe(takeUntil(this.destroyed$)).subscribe(softwareToInstall => {
      this.emitSoftwareInstall(softwareToInstall);
      this.showSoftwareChanges.emit();
    });

    choiceEmitter
      .pipe(
        this.isSoftwareInstalledOnDevicePipe(updateInstallableList$),
        takeUntil(this.destroyed$)
      )
      .subscribe(({ item }) =>
        updateInstallableList$.next({
          object: item,
          template: this.alreadyInstalledWarningTemplate,
          mapper: object => {
            object.installed = true;
            return object;
          }
        })
      );
  }

  removeSoftware(softwareToRemove) {
    this.emitSoftwareRemoval([softwareToRemove]);
  }

  getInstallableSoftwareListWithVersions$(searchTerm$: BehaviorSubject<FilterCriteria>) {
    const installedSoftwareNames = (this.softwareList || []).map(s => s.name);
    return searchTerm$.pipe(
      distinctUntilChanged(),
      switchMap(searchTerm =>
        this.repository.listRepositoryEntries(RepositoryType.SOFTWARE, {
          query: searchTerm.softwareType
            ? this.queriesUtil.addAndFilter(this.typesQuery, {
                softwareType: searchTerm.softwareType
              })
            : this.typesQuery,
          partialName: searchTerm?.name,
          params: { pageSize: 100 }
        })
      ),
      map(({ data }) => data),
      map(softwareList => {
        return softwareList.filter(software => {
          return !installedSoftwareNames.includes(software.name);
        });
      }),
      map(softwareList => this.attachVersions(softwareList)),
      shareReplay(1)
    );
  }

  getSingleSoftwareWithVersions$(software: DeviceSoftware) {
    return from(
      this.repository.listRepositoryEntries(RepositoryType.SOFTWARE, {
        query: { name: software.name }
      })
    ).pipe(
      map(({ data }) => data),
      map(softwareList => this.attachVersions(softwareList)),
      shareReplay(1)
    );
  }

  attachVersions(softwareList: IManagedObject[]) {
    softwareList.forEach(software => {
      software.versions = this.repository.listBaseVersions(software);
    });
    return softwareList;
  }

  displaySoftwareSelectModal(initialStateOverrides) {
    const initialState = {
      repositoryType: RepositoryType.SOFTWARE,
      mode: ModalSelectionMode.MULTI,
      icon: 'c8y-tools',
      disableSelected: false,
      selected: this.softwareList,
      ...initialStateOverrides
    };
    const modal = this.bsModal.show(RepositorySelectModalComponent, {
      ignoreBackdropClick: true,
      class: 'modal-sm',
      initialState
    });

    if (initialStateOverrides.repositoryEntriesWithVersionsFn$) {
      modal.content.repositoryEntriesWithVersions$ =
        initialStateOverrides.repositoryEntriesWithVersionsFn$(modal);
    }

    this.modalSearch = modal.content.search.bind(modal.content);

    modal.content.load.next();
    return {
      resultEmitter: modal.content.resultEmitter,
      choiceEmitter: modal.content.onChoiceUpdated,
      updateInstallableList$: modal.content.updateInstallableList$
    };
  }

  search(filterCriteria: FilterCriteria) {
    if (this.modalSearch) {
      this.modalSearch(filterCriteria);
    }
  }

  emitSoftwareInstall(items: DeviceSoftware[]) {
    this.changes.emit(
      items.map(item => {
        return { ...item, action: 'install' };
      })
    );
  }

  emitSoftwareRemoval(items: DeviceSoftware[]) {
    this.changes.emit(
      items.map(item => {
        return { ...item, action: 'delete' };
      })
    );
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  private isSoftwareInstalledOnDevicePipe(updateInstallableList$: Subject<IUpdateItemEvent<any>>) {
    return pipe(
      tap((item: ISelectModalObject) =>
        updateInstallableList$.next({ object: item, template: this.loadingTemplate })
      ),
      map(item => ({
        item,
        software: (
          (item.options || []).find(option => option.obj.id === (item as any).selectedId) || {}
        ).obj
      })),
      mergeMap(({ item, software }) =>
        from(
          this.repository.isSoftwareInstalledOnDevice(this.device.id, software as DeviceSoftware)
        ).pipe(map(installed => ({ item, installed })))
      ),
      tap(({ item }) => updateInstallableList$.next({ object: item })),
      filter(({ installed }) => !!installed)
    );
  }
}
