import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { map } from 'rxjs/operators';
import { gettext } from '@c8y/ngx-components';
import { DeviceSoftwareChange } from '@c8y/ngx-components/repository/shared';

@Component({
  selector: 'c8y-device-software-changes',
  templateUrl: 'device-software-changes.component.html'
})
export class DeviceSoftwareChangesComponent implements OnChanges {
  @Input() changes: DeviceSoftwareChange[];
  @Input() changesInProgress: boolean;
  @Output() clear: EventEmitter<void> = new EventEmitter();
  @Output() drop: EventEmitter<DeviceSoftwareChange> = new EventEmitter();
  @Output() apply: EventEmitter<void> = new EventEmitter();
  @Output() hideSoftwareChanges = new EventEmitter<void>();

  get applyButtonLabel() {
    return this.changesInProgress ? gettext('Applying changesâ€¦') : gettext('Apply changes');
  }

  changes$ = new BehaviorSubject<DeviceSoftwareChange[]>([]);
  installations$ = this.changes$.pipe(
    map(changes => {
      return changes.filter(change => change.action === 'install');
    })
  );

  removals$ = this.changes$.pipe(
    map(changes => {
      return changes.filter(change => change.action === 'delete');
    })
  );

  ngOnChanges(changes: SimpleChanges) {
    if (changes.changes) {
      this.changes$.next(changes.changes.currentValue);
    }
  }
}
