import { ColumnConfig, gettext } from '@c8y/ngx-components';
import { BaseDeviceGridColumn } from '@c8y/ngx-components/device-grid';
import { VersionsCellRendererComponent } from './versions.cell-renderer.component';

export class VersionsGridColumn extends BaseDeviceGridColumn {
  constructor(initialColumnConfig?: ColumnConfig) {
    super(initialColumnConfig);
    this.name = 'versions';
    this.header = gettext('Versions');
    this.cellRendererComponent = VersionsCellRendererComponent;
    this.sortable = false;
  }
}
