import {
  AfterContentInit,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output
} from '@angular/core';
import { IManagedObject, IResultList } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';
import {
  DeviceSoftware,
  DeviceSoftwareChange,
  FilterCriteria
} from '@c8y/ngx-components/repository/shared';
import { filter, get } from 'lodash-es';
import { BehaviorSubject, combineLatest, Observable, of, Subject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  map,
  share,
  switchMap,
  takeUntil,
  tap
} from 'rxjs/operators';
import { DeviceSoftwareService } from './device-software.service';

@Component({
  selector: 'c8y-device-software-list',
  templateUrl: 'device-software-list.component.html'
})
export class DeviceSoftwareListComponent implements OnInit, AfterContentInit, OnDestroy {
  @Input() set softwareList(softwareList: DeviceSoftware[]) {
    if (softwareList !== null) {
      this.legacySoftwareList$.next(softwareList);
    }
  }
  @Input() device: IManagedObject;
  @Input() deviceSoftwareChanges: DeviceSoftwareChange[];
  @Input() filterCriteria$: Observable<FilterCriteria> = of(null);
  @Output() update = new EventEmitter<DeviceSoftware>();
  @Output() remove = new EventEmitter<DeviceSoftware>();
  @Output() onListEmpty: EventEmitter<boolean> = new EventEmitter();
  softwareItems$: Observable<IResultList<DeviceSoftware>>;
  showUpdate: boolean;
  showRemove: boolean;
  emptyList: boolean;
  noSearchResults: boolean;
  alreadyInstalledMessage = gettext('This software is already installed on the device');
  supportsSoftwareOperations: boolean = false;

  private readonly operationTypes = ['c8y_SoftwareUpdate', 'c8y_SoftwareList', 'c8y_Software'];

  private legacySoftwareList$: BehaviorSubject<DeviceSoftware[]> = new BehaviorSubject([]);
  private destroyed$ = new Subject<void>();

  constructor(private deviceSoftwareService: DeviceSoftwareService) {}

  ngOnInit(): void {
    this.softwareItems$ = combineLatest([
      this.filterCriteria$.pipe(debounceTime(300), distinctUntilChanged()),
      this.legacySoftwareList$
    ]).pipe(
      switchMap(([filterCriteria, legacySoftwareList]) =>
        this.deviceSoftwareService
          .getSoftwareList(this.device?.id, filterCriteria, legacySoftwareList)
          .pipe(map(resultList => ({ resultList, filterCriteria })))
      ),
      tap(({ resultList, filterCriteria }) => {
        this.notifyListEmpty(!resultList?.paging?.totalPages && !filterCriteria);
        this.noSearchResults = !resultList?.paging?.totalPages && !!filterCriteria;
      }),
      map(({ resultList }) => resultList),
      share(),
      takeUntil(this.destroyed$)
    );
    const supportedOperations = get(this.device, 'c8y_SupportedOperations', []);
    this.supportsSoftwareOperations = this.operationTypes.some(
      operationType => supportedOperations.indexOf(operationType) > -1
    );
  }

  ngAfterContentInit() {
    this.showUpdate = this.update.observers.length > 0;
    this.showRemove = this.remove.observers.length > 0;
  }

  isSoftwareGoingToBeChanged(software: DeviceSoftware): boolean {
    const relevantChanges = filter(this.deviceSoftwareChanges, software);
    return relevantChanges.length > 0;
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  private notifyListEmpty(isEmpty: boolean): void {
    this.emptyList = isEmpty;
    this.onListEmpty.emit(isEmpty);
  }
}
