import { Component, Output, EventEmitter, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { BsModalRef } from 'ngx-bootstrap/modal';
import {
  RepositoryType,
  ModalModel,
  RepositoryCategory,
  RepositoryService
} from '@c8y/ngx-components/repository/shared';
import { AlertService, gettext } from '@c8y/ngx-components';
import { isUndefined, get, assign } from 'lodash-es';
import { BehaviorSubject, Subscription, from } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap, switchMap } from 'rxjs/operators';
import { PickedFiles } from '@c8y/ngx-components';
import { IManagedObject } from '@c8y/client';

@Component({
  selector: 'c8y-add-software-software-modal',
  templateUrl: 'add-software-modal.component.html'
})
export class AddSoftwareModalComponent {
  @ViewChild('softwareForm', { static: false }) form: NgForm;
  @Output() saved: EventEmitter<RepositoryCategory> = new EventEmitter<RepositoryCategory>();
  onInput: BehaviorSubject<string> = new BehaviorSubject<string>('');
  model: ModalModel = {
    selected: undefined,
    version: undefined,
    description: undefined,
    deviceType: undefined,
    softwareType: undefined,
    binary: {
      file: undefined,
      url: undefined
    }
  };

  softwaresResult;
  saving = false;
  softwarePreselected: boolean = false;
  textForSoftwareUrlPopover: string =
    gettext(`Path for binaries can vary depending on device agent implementation, for example:
    /software/binaries/software1.bin
    https://software/binary/123
    ftp://software/binary/123.tar.gz
  `);
  private inputSubscription$: Subscription;

  constructor(
    private modal: BsModalRef,
    private repositoryService: RepositoryService,
    private alert: AlertService
  ) {}

  ngOnInit() {
    this.setInitialState();
    this.loadSoftwares();
  }

  setInitialState() {
    if (this.model.selected) {
      this.softwarePreselected = true;
    }
  }

  loadSoftwares() {
    this.inputSubscription$ = this.onInput
      .pipe(
        tap(() => {
          if (!this.softwarePreselected) {
            this.model.description = null;
            if (this.form) {
              this.form.form.get('description').reset();
            }
          }
        }),
        debounceTime(300),
        distinctUntilChanged(),
        switchMap(searchStr => this.getSoftwareResult(searchStr))
      )
      .subscribe(result => {
        this.softwaresResult = result;
      });
  }

  getSoftwareResult(searchStr: string) {
    return from(
      this.repositoryService.listRepositoryEntries(RepositoryType.SOFTWARE, {
        partialName: searchStr,
        skipLegacy: true
      })
    );
  }

  async save() {
    this.saving = true;
    this.repositoryService
      .create(this.model, RepositoryType.SOFTWARE)
      .then(savedSoftware => {
        this.successMsg();
        this.saving = false;
        this.saved.next(savedSoftware);
        this.cancel();
      })
      .catch(e => {
        this.saving = false;
        this.saved.error(e);
        this.cancel();
      });
  }

  successMsg() {
    const msg = gettext('Software added.');
    this.alert.success(msg);
  }

  cancel() {
    this.modal.hide();
    this.saved.complete();
  }

  ngOnDestroy() {
    this.inputSubscription$.unsubscribe();
  }

  onFile(dropped: PickedFiles) {
    if (!isUndefined(dropped.url)) {
      this.model.binary = {
        url: dropped.url
      };
      return;
    } else if (dropped.droppedFiles) {
      this.model.binary = {
        file: dropped.droppedFiles[0].file
      };
      return;
    } else {
      this.model.binary = {
        file: undefined,
        url: undefined
      };
    }
  }

  onSoftwareSelect(software: IManagedObject) {
    assign(this.model, {
      selected: software,
      description: software.description,
      deviceType: get(software, 'c8y_Filter.type'),
      softwareType: software
    });
  }
}
