import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IManagedObject, InventoryService, IOperation, OperationStatus } from '@c8y/client';
import {
  AdvancedSoftwareService,
  DeviceSoftware,
  DeviceSoftwareChange,
  RepositoryService,
  RepositoryType
} from '@c8y/ngx-components/repository/shared';
import { BehaviorSubject, Observable } from 'rxjs';
import { filter, map, switchMap, take } from 'rxjs/operators';
import { DeviceSoftwareService } from './device-software.service';

@Component({
  selector: 'c8y-software-device-tab',
  templateUrl: 'software-device-tab.component.html'
})
export class SoftwareDeviceTabComponent implements OnInit {
  deviceId: string | number = this.route.snapshot.parent.data.contextData.id;
  device$ = new BehaviorSubject<IManagedObject>(this.route.snapshot.parent.data.contextData);
  typesQuery$: Observable<object> = this.device$.pipe(
    map(device => {
      const deviceTypeQuery = this.repository.getDeviceTypeQuery(RepositoryType.SOFTWARE, device);
      return this.repository.getSoftwareTypeQuery(device, deviceTypeQuery);
    })
  );
  list$: Observable<DeviceSoftware[]> = this.device$.pipe(
    switchMap(device =>
      this.advancedSoftwareService
        .isASMAvailable()
        .then(isASMAvailable => ({ isASMAvailable, device }))
    ),
    map(({ isASMAvailable, device }) =>
      // with ASM available software items will be retrieved directly in the
      // device-software-list component
      isASMAvailable ? undefined : this.repository.getDeviceSoftwareList(device)
    )
  );
  changes$ = new BehaviorSubject<DeviceSoftwareChange[]>([]);
  changesOperation$ = new BehaviorSubject<IOperation>(null);
  changesInProgress$: Observable<boolean> = this.changesOperation$.pipe(
    map(operation => this.isInProgress(operation))
  );
  reloading: boolean = false;
  showSoftwareChanges: boolean = false;

  constructor(
    private route: ActivatedRoute,
    private repository: RepositoryService,
    private inventory: InventoryService,
    private deviceSoftwareService: DeviceSoftwareService,
    private advancedSoftwareService: AdvancedSoftwareService
  ) {}

  async ngOnInit() {
    await this.loadDevice();
    await this.loadOperation();
  }

  addChanges(requestedChanges: DeviceSoftwareChange[]) {
    let stagedChanges = [...this.changes$.value];
    requestedChanges.forEach(requestedChange => {
      const alreadyStaged = stagedChanges.some(stagedChange =>
        this.areSameChanges(stagedChange, requestedChange)
      );
      if (!alreadyStaged) {
        stagedChanges = [...stagedChanges, requestedChange];
      }
    });
    this.changes$.next(stagedChanges);
  }

  dropChange(changeToBeDropped: DeviceSoftwareChange) {
    let stagedChanges = [...this.changes$.value];
    stagedChanges = stagedChanges.filter(
      stagedChange => !this.areSameChanges(stagedChange, changeToBeDropped)
    );
    this.changes$.next(stagedChanges);
  }

  areSameChanges(change1: DeviceSoftwareChange, change2: DeviceSoftwareChange) {
    return (
      change1.name === change2.name &&
      change1.version === change2.version &&
      change1.action === change2.action
    );
  }

  clearChanges() {
    this.changes$.next([]);
  }

  async loadDevice() {
    this.reloading = true;
    this.deviceSoftwareService.reload();
    const device = await Promise.all([
      this.deviceSoftwareService.loading$
        .pipe(
          filter(loading => !loading),
          take(1)
        )
        .toPromise(),
      this.inventory.detail(this.deviceId, { withChildren: false }).then(result => result.data)
    ]).then(([_, dvc]) => dvc);
    this.device$.next(device);
    this.reloading = false;
  }

  async applyChanges() {
    const operation = await this.repository.createSoftwareUpdateOperation(
      this.device$.value,
      this.changes$.value
    );
    await this.trackOperation(operation);
  }

  private async loadOperation() {
    const operation = await this.repository.getLastSoftwareUpdateOperation(this.deviceId);
    await this.trackOperation(operation);
  }

  private async trackOperation(operation: IOperation) {
    this.changesOperation$.next(operation);

    if (this.isInProgress(operation)) {
      await this.displayChangesFromOperation(operation);
      this.repository.observeOperation(operation).subscribe(
        operationUpdate => {
          this.changesOperation$.next(operationUpdate);
          if (operationUpdate.status === OperationStatus.SUCCESSFUL) {
            this.clearChanges();
            this.loadDevice();
          }
        },
        operationUpdate => {
          this.changesOperation$.next(operationUpdate);
        }
      );
    }
  }

  private async displayChangesFromOperation(operation: IOperation) {
    const changes = await this.repository.getDeviceSoftwareChangesFromOperation(
      operation,
      this.device$.value
    );
    this.changes$.next(changes);
  }

  private isInProgress(operation: IOperation) {
    return (
      operation && [OperationStatus.PENDING, OperationStatus.EXECUTING].includes(operation.status)
    );
  }
}
