import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory } from '@c8y/ngx-components';

@Injectable()
export class SoftwareRepositoryNavigationFactory implements NavigatorNodeFactory {
  node: NavigatorNode;

  constructor() {
    this.node = new NavigatorNode({
      label: gettext('Software repository'),
      path: 'software',
      icon: 'c8y-tools',
      parent: gettext('Management'),
      priority: 900
    });
  }

  get(): NavigatorNode {
    return this.node;
  }
}
