import { Component } from '@angular/core';
import { IManagedObject } from '@c8y/client';
import { CellRendererContext, memoize } from '@c8y/ngx-components';
import { property } from 'lodash-es';
import { Observable } from 'rxjs';
import { shareReplay } from 'rxjs/operators';
import { RepositoryService } from '@c8y/ngx-components/repository/shared';

@Component({
  templateUrl: './versions.cell-renderer.component.html'
})
export class VersionsCellRendererComponent {
  isLegacy = this.repositoryService.isLegacyEntry.bind(this.repositoryService);
  software;
  constructor(public context: CellRendererContext, private repositoryService: RepositoryService) {
    this.software = context.item;
  }

  @memoize(property('id'))
  getBaseVersionsCount$(software: IManagedObject): Observable<number | string> {
    return this.repositoryService.getBaseVersionsCount$(software).pipe(shareReplay(1));
  }
}
